<template>
  <div id="app">
    <mu-appbar title="OfficeCheckIn">
      <mu-icon-button icon='menu' slot="left" @click="toggle(true)"/>
     <mu-circular-progress :size="30" :strokeWidth="5" color="white" slot="right"/>
    </mu-appbar>
      <mu-list>
        <mu-sub-header>在线人数</mu-sub-header>
        <mu-list-item :title="key" v-for="(value,key) in userList">
          <mu-avatar :src="demoimg" slot="leftAvatar"/>
          <mu-icon value="chat_bubble" slot="right" @click="alarm(key)"/>
        </mu-list-item>
        
      </mu-list>
      
        
          <mu-flexbox class="mt8">
           
            <mu-flexbox-item order="1" class="flex-demo">
             <mu-raised-button label="刷新" class="refresh-button" primary @click="getUserList"/>
            </mu-flexbox-item>
          </mu-flexbox>
            <mu-dialog :open="dialog" title="Dialog" @close="close">
              已发出提醒
              <mu-flat-button slot="actions" @click="close" primary label="取消"/>
              <mu-flat-button slot="actions" primary @click="close" label="确定"/>
            </mu-dialog>
             <mu-dialog :open="login" title="登录">
              <mu-text-field hintText="你的名字" type="text" v-model="username" errorText="这是必填项"/><br/>
              <mu-flat-button label="确定" slot="actions" primary @click="closelogin"/>
            </mu-dialog>
            
              <mu-dialog :open="alert" title="提醒">
               开门！！！！！
                <mu-flat-button label="确定" slot="actions" primary @click="closealert"/>
              </mu-dialog>
              <mu-drawer :open="draweropen" :docked="docked" @close="toggle(true)">
                <mu-list >
                  <mu-list-item title="开启自动刷新" @click="beginRefresh"/>
                  <mu-list-item title="关闭自动刷新" @click="stopRefresh"/>
                 
                </mu-list>
              </mu-drawer>
  </div>


</template>

<script>
  export default {
    data() {
      return {
        dialog: false,
        login: true,
        alert: false,
        userList: [{
          'username': "1"
        }],
        demoimg:require('./assets/img/icon_mobile-phone.png'),
        username: this.initUsername(),
        draweropen: false,
        docked: false
      }
    },
    mounted() {
      this.getUserList();
      this.autoRefresh()
     
    },
    methods:{
      autoRefresh () {
        this.timer = setInterval(()=>{
          this.getUserList()
        },5000)
      },
      stopRefresh (){
        clearInterval(this.timer)
        this.timer = null
      },
      beginRefresh () {
        if(this.timer == null)
          this.autoRefresh()
      },
      initUsername() {
        if(localStorage.hasOwnProperty('username')) {
          return localStorage['username']
        } else {
          return null
        }
      },
      getUserList() {
        if(this.username != null) {
           this.$http.get('http://checkin.kyjocode.cn/?username='+ this.username).then((res)=>{
            // console.log(res)
            this.userList = res.body.userList
            console.log(this.userList)
            this.getAlarm(res.body.isAlarm)
          })
        } else {
            this.$http.get('http://checkin.kyjocode.cn').then((res)=>{
            // console.log(res)
              this.userList = res.body.userList
              console.log(this.userList)
            })
        }
       
      },
      alarm(username) {
         this.$http.get('http://checkin.kyjocode.cn/alarm?username='+ username).then((res)=>{
           console.log(res)
           if(res.body.status == 'accept') {
             this.dialog = true
           }
        })
      },
      open () {
        this.dialog = true
      },
      close () {
        this.dialog = false
      },
      closelogin () {
        this.login = false;
        localStorage.username = this.username

      },
       closealert () {
        this.alert = false;
      },
      getAlarm (isAlarm) {
        if(isAlarm) {
          navigator.vibrate(1000);
          this.alert = true
        }
         
      },
      toggle (flag) {
      this.draweropen = !this.draweropen
      this.docked = !flag
    }
    }
  }
</script>
<style>
  .refresh-button {
      margin: 0 auto; 
  }
  .mt8 {
  margin-top: 8px;
}
.flex-demo {
 
 
  text-align: center;
  line-height: 32px;
}
</style>


